@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Play:wght@700&display=swap);
*{
  margin: 0; padding: 0; box-sizing: border-box;
  font-family: 'Architects Daughter', cursive;
  font-family: 'Play', sans-serif;
}
.carousel-item img{
  width: 100%;
  height: 100vh;
}
.img-lhr{
  min-width:55%;
  max-height: 295px;
}
.btn-a:hover{
  color: blue;
}

.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  align-items: center;
  pointer-events: none;
  border-radius: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 15s linear;
            animation: App-logo-spin infinite 15s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: red;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.brand-name{
  color: blueviolet;
  font-weight: bolder;
}
.atta{
  background: #a6dcef;
}
.navbar{
  background-color: #fff!important;
}
.navbar-brand{
  font-size: 2rem!important;
  color: #565387!important;

}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold; border-bottom: 2px solid#565387;
}
.navbar a:hover{
  color: #3498db!important;
}
.App_logo{
  height: 7vmin;
  
  pointer-events: none;
  border-radius: 50%;
}
@media (prefers-reduced-motion: no-preference) {
  .App_logo {
    -webkit-animation: App-logo-spin infinite 15s linear;
            animation: App-logo-spin infinite 15s linear;
  }
}

@-webkit-keyframes App_logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App_logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-logo{
  width: 90%;
  
}

